@media (max-width: 768px) and (orientation: landscape) {
  .screen__footer-note span {
    margin-right: 0;
  }
}
.screen__footer-note span.on-top {
  display: none;
}
.section-top .screen__footer-note span.on-top {
  display: inline;
}
.screen__footer-note span.on-prizes {
  display: none;
}
.section-prizes .screen__footer-note span.on-prizes {
  display: inline;
}

.screen--story {
  background-color: #5f458c;
  background-image: url("../img/slide1.jpg"), url("../img/slide2.jpg"), url("../img/slide3.jpg"), url("../img/slide4.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
}
@media (orientation: portrait) {
  .screen--story {
    background-size: auto calc(100vh - 13.8rem), 100% 25rem;
    background-position: top center, bottom center;
  }
}

.screen--prizes {
  background-color: #5f458c;
}

.screen--game {
  position: relative;
  background-color: #5f458c;
}
.screen--game::before {
  content: "";
  position: absolute;
  right: -8.5rem;
  bottom: 0;
  width: 32.7rem;
  height: 36.9rem;
  background-image: url("../img/sonya.png");
  background-repeat: no-repeat;
  background-size: contain;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .screen--game::before {
    background-image: url("../img/sonya@2x.png");
  }
}
@media (min-width: 2000px) {
  .screen--game::before {
    right: -85px;
    width: 327px;
    height: 369px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .screen--game::before {
    opacity: 0;
  }
}

.screen--result {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #5f458c;
  opacity: 0;
  pointer-events: none;
}
.screen--result.screen--show {
  opacity: 1;
  pointer-events: auto;
}

@keyframes animateText {
  0% {
    opacity: 0;
    transform: translateX(5rem);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes animatePoint {
  0% {
    transform: scale(0);
    color: transparent;
    opacity: 1;
  }
  50% {
    transform: scale(1.3);
  }
  100% {
    opacity: 1;
    transform: scale(1);
    color: #ffffff;
  }
}
@keyframes animateBtn {
  0% {
    width: 6rem;
    opacity: 0;
    margin-left: calc(100% - 12rem);
  }
  1% {
    opacity: 1;
  }
  100% {
    width: 100%;
    opacity: 1;
    margin-left: 0;
  }
}
.screen--rules {
  background-color: #5f458c;
}
.screen--rules .btn {
  color: transparent;
  transition: color 0.2s ease-out 2.3s;
}
.screen--rules .btn::before {
  opacity: 0;
}
.screen--rules.active .rules__item:nth-child(1)::before {
  opacity: 0;
  animation: animatePoint 0.4s ease-out 0.85s forwards;
}
.screen--rules.active .rules__item:nth-child(1) p {
  opacity: 0;
  animation: animateText 0.4s ease-out 0.85s forwards;
}
.screen--rules.active .rules__item:nth-child(2)::before {
  opacity: 0;
  animation: animatePoint 0.4s ease-out 1s forwards;
}
.screen--rules.active .rules__item:nth-child(2) p {
  opacity: 0;
  animation: animateText 0.4s ease-out 1s forwards;
}
.screen--rules.active .rules__item:nth-child(3)::before {
  opacity: 0;
  animation: animatePoint 0.4s ease-out 1.15s forwards;
}
.screen--rules.active .rules__item:nth-child(3) p {
  opacity: 0;
  animation: animateText 0.4s ease-out 1.15s forwards;
}
.screen--rules.active .rules__item:nth-child(4)::before {
  opacity: 0;
  animation: animatePoint 0.4s ease-out 1.3s forwards;
}
.screen--rules.active .rules__item:nth-child(4) p {
  opacity: 0;
  animation: animateText 0.4s ease-out 1.3s forwards;
}
.screen--rules.active .btn {
  color: #5f458c;
}
.screen--rules.active .btn::before {
  animation: animateBtn 0.5s ease-out 1.8s forwards;
}

.per-letter-anim__word {
  overflow: hidden;
  display: inline-block;
  white-space: nowrap;
}
.per-letter-anim__word > span {
  padding-top: 2rem;
  transform: translate3d(0, 100%, 0);
  display: inline-block;
}

.screen.active .per-letter-anim__word > span,
.screen.active .per-letter-anim__word > span {
  transform: translate3d(0, 0, 0);
}